          p2: "发布日期： 2019/09/03",
           path:"/Dynamic-One"
        },
        {
          pic: "../../static/img/10.png",
          h3: "省总工会党组召开 “不忘初心、牢记使......",
          p1:
            "8月26日，省总工会党组召开“不忘初心、牢记使命”主题教育专题民主生活会。省委常委、省总工会主席马吉孝出席会议并讲话，省委第八巡回指导组组长李勇作点评，省总工会党组书记、常务副主席陈志忠主持会议,会议通报了省总开展“不忘初心、牢记使命”主题教育工作情况",
