        }
      ],
      showFilter: false,
      allLoaded: false,
      bottomPullText: "上拉加载更多",
      data: null
    };
  },
  methods: {
    loadData() {
      this.allLoaded = false;
      this.bottomPullText = "上拉加载更多";
      // 拉取商家信息;
    },
    loadMore() {
      if (!this.allLoaded) {
        this.$axios.post();
      }
    }
  }
