</script>

<style scoped>
.line {
  width: 100%;
  height: 9rem;
}
.line .img {
  width: 38%;
  height: 8rem;
}
.line .img img {
  width: 100%;
  height: 8rem;
}
.line .know {
  width: 58%;
  height: 8rem;
}
.line .know h3 {
  font-size: 0.7rem;
  font-weight: bold;
  color: black;
  align-self: flex-start;
}
.line .know p {
  font-size: 0.1rem;
  text-indent: 1rem;
  color: #909090;
  line-height: 0.75rem;
  text-align: left;
}
.no {
  width: 100%;
  height: 2rem;
  text-align: center;
  line-height: 2rem;
  font-size: 0.82rem;
  color: #777777;
}
</style>


